import React from 'react';
import { FaTag, FaCheck, FaTrash, FaPencilAlt } from 'react-icons/fa';

const ActiveTodo = ({ id, text, color, onComplete, onDelete }) => {
  return (
    <div className="active-todo">
      <div className="active-todo__icon-wrapper" onClick={() => onComplete(id)}>
        <FaCheck />
      </div>
      <div className="active-todo__content">{text}</div>
      <div className="active-todo__actions">
        <FaTag className={color || 'grey'} />
        <FaTrash className="red" onClick={() => onDelete(id)} />
      </div>
      <div className="active-todo__date">Creation date: {new Date(id).toLocaleDateString()}</div>
    </div>
  );
};

export default ActiveTodo;
